@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;400,600&display=swap");

* {
  font-family: "Roboto", sans-serif !important;
}

h1,
h2,
h3,
h4 {
  margin: 0px;
}

.App {
  background-color: #f5f5f5;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.App-Margin-L10 {
  margin-left: 10px;
}

.App-Margin-R10 {
  margin-right: 10px;
}

.App-Header {
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #003f8c;
}

.App-Header h1 {
  font-size: 25px;
  font-weight: 400;
  color: #ffffff;
  margin-left: 15px;
}

.App-Main {
  flex: 1;
  width: 88%;
  max-width: 1280px;
  background-color: #ffffff;
  margin: 30px auto;
  padding: 20px;
  border: 1px solid #c2c8d0;
}

.App-Form-Input {
  height: 30px;
}

.App-Add-New-Line-Btn {
  position: relative;
  display: inline-block;
}

.App-Tooltip {
  visibility: hidden;
  width: 120px;
  background-color: #003f8c;
  color: #fff;
  text-align: center;
  border-radius: 6px;
  padding: 5px 0;
  position: absolute;
  z-index: 1;
  bottom: 150%;
  left: 50%;
  margin-left: -60px;
}

.App-Tooltip:after {
  content: "";
  position: absolute;
  top: 100%;
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: #003f8c transparent transparent transparent;
}

.App-Add-New-Line-Btn:hover .App-Tooltip {
  visibility: visible;
}

.App-Footer {
  height: 40px;
  text-align: center;
  background-color: #003f8c;
  color: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 14px;
  font-weight: 300;
}

.input-group-text,
.form-control,
.btn,
.modal-content {
  border-radius: 0 !important;
}

.input-group-text {
  background-color: #ffffff;
}

.form-control {
  border-color: #c2c8d0 !important;
}

.btn-default,
.btn-default:hover,
.btn-default:active,
.btn-default:focus-visible {
  background-color: #ffffff !important;
  border-color: #c2c8d0 !important;
  color: #212529 !important;
  font-weight: 600;
}

/* The SIMPLE-TOAST - position it at the bottom and in the middle of the screen */
#simpleToast {
  visibility: hidden; /* Hidden by default. Visible on click */
  min-width: 250px; /* Set a default minimum width */
  margin-left: -125px; /* Divide value of min-width by 2 */
  background-color: #003f8c; /* Background color */
  color: #fff; /* White text color */
  text-align: center; /* Centered text */
  border-radius: 2px; /* Rounded borders */
  padding: 16px; /* Padding */
  position: fixed; /* Sit on top of the screen */
  z-index: 9999; /* Add a z-index if needed */
  left: 50%; /* Center the snackbar */
  bottom: 30px; /* 30px from the bottom */
  font-family: monospace;
  display: inline-flex;
  line-hight: 12px;
}
#simpleToast span {
  margin-left: 8px;
  margin-top: 2px;
}

/* Show the SIMPLE-TOAST when clicking on a button (class added with JavaScript) */
#simpleToast.show {
  visibility: visible; /* Show the SIMPLE-TOAST */
  /* Add animation: Take 0.5 seconds to fade in and out the SIMPLE-TOAST.
  However, delay the fade out process for 2.5 seconds */
  -webkit-animation: fadein 0.5s, fadeout 0.5s 2.5s;
  animation: fadein 0.5s, fadeout 0.5s 2.5s;
}

/* Animations to fade the SIMPLE-TOAST in and out */
@-webkit-keyframes fadein {
  from {
    bottom: 0;
    opacity: 0;
  }
  to {
    bottom: 30px;
    opacity: 1;
  }
}

@keyframes fadein {
  from {
    bottom: 0;
    opacity: 0;
  }
  to {
    bottom: 30px;
    opacity: 1;
  }
}

@-webkit-keyframes fadeout {
  from {
    bottom: 30px;
    opacity: 1;
  }
  to {
    bottom: 0;
    opacity: 0;
  }
}

@keyframes fadeout {
  from {
    bottom: 30px;
    opacity: 1;
  }
  to {
    bottom: 0;
    opacity: 0;
  }
}
